// Color Variables - Soft Natural Palette
$color-primary: #6b8068;        // Muted forest green
$color-secondary: #d4c4b0;      // Warm sand
$color-accent: #a08876;         // Earth brown
$color-text: #2d2d2d;           // Soft black
$color-text-light: #6b6b6b;     // Medium gray
$color-background: #faf8f5;     // Cream paper
$color-card: #ffffff;           // Pure white
$color-border: #e5ddd3;         // Light tan

// Typography
$font-primary: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$font-serif: 'Georgia', 'Crimson Text', serif;

$font-size-base: 18px;
$font-size-small: 16px;
$font-size-large: 24px;
$font-size-xlarge: 40px;

$line-height-base: 1.8;
$line-height-tight: 1.3;

// Spacing
$spacing-xs: 0.5rem;
$spacing-sm: 1rem;
$spacing-md: 2rem;
$spacing-lg: 3.5rem;
$spacing-xl: 5rem;

// Breakpoints
$breakpoint-mobile: 768px;
$breakpoint-tablet: 1024px;
$breakpoint-desktop: 1400px;

// Border radius
$radius-sm: 2px;
$radius-md: 6px;
$radius-lg: 12px;
